import { Link } from 'gatsby';
import React from 'react'
import './style.css';
import { ButtonTypes } from './constants';

export const CustomLink = (props: any) => {
  return (
    <div>
        <Link style={props.style} className={`custom-btn ${ButtonTypes[props.type] || ButtonTypes['link']} ${props.active ? 'active' : ''} ${props.className || ""}`} to={props.to}>
            {props.children}
        </Link>
    </div>
  )
}

const Button = (props: any) => {
  return (
    <div className={`${props.className || ""}`}>
        <Link className={`custom-button button-secondary px-2 py-1`} to={props.to}>
            {props.children}
        </Link>
    </div>
  )
}

export default Button;
